import React, { useState, useRef } from 'react';
import Vidosik from './Vidosik';

export default function VideoList({ displayVideo, saveVideo }) {
  const [vidosy, setVideo] = useState();
  let checked = false;
  const searchInput = useRef('');
  let results = [];
  let cc = 'any';

  // Function For YouTube Videos Serching
  function searchVideo(e) {
    e.preventDefault();
    // prepare the request
    let request = window.gapi.client.youtube.search.list({
      part: 'snippet',
      type: 'video',
      q: encodeURIComponent(searchInput.current.value).replace(/%20/g, '+'),
      maxResults: 10,
      videoCaption: cc,
      videoSyndicated: 'true',
      order: 'viewCount'
      // regionCode: 'UA'
    });

    // execute the request
    request.execute(function(response) {
      // console.log(response);
      let res = response.items;
      console.log(res);
      res.forEach(function(item, index) {
        let idishnik = item.id.videoId;
        results.push(
          <div className="fetched-videos">
            <div id="title">{item.snippet.title}</div>
            <Vidosik
              id={'vidos-' + index}
              src={'https://www.youtube.com/embed/' + item.id.videoId}
            />
            <div className="right-buttons">
              <button
                className="display"
                id={'display-' + index}
                onClick={displayVideo.bind(this, idishnik)}
              >
                Display
              </button>
              <button
                id={'save-' + index}
                onClick={saveVideo.bind(this, idishnik)}
              >
                Save
              </button>
            </div>
          </div>
        );
      });
      // console.log(results);
      setVideo(results);
    });
  }
  return (
    <section className="videos-list">
      <form id="right-form" onSubmit={e => searchVideo(e)}>
        <div className="subtitles">
          <label htmlFor="subtitles">CC</label>
          <input
            type="checkbox"
            name="subtitles"
            id="subtitles"
            onChange={() => {
              checked = !checked;
              if (checked === true) {
                cc = 'closedCaption';
              } else {
                cc = 'any';
              }
              // console.log(checked);
              // console.log(cc);
            }}
          />
        </div>
        <input
          type="text"
          placeholder="YouTube Search"
          id="search"
          ref={searchInput}
        />
        <input type="submit" value="Search" />
      </form>
      <div id="results">{vidosy}</div>
    </section>
  );
}
